import { Link } from 'gatsby'
import React from 'react'
import "../styles/nav.css"

export default function Nav() {
    return (
        <nav className='flex' >
            <ul className='items-center m-auto flex gap-8 justify-center     text-white' >
                <li className=' my-auto'><Link to="/">Home</Link></li>
                <li className=' my-auto'><Link  to="/blog">Blog</Link></li>
                <li className='my-auto'><Link  to="/contact">Contact</Link></li>
                <li className=' my-auto'><Link  to="/about">About</Link></li>
                <li className=' my-auto border-2 rounded-lg radius-1 border-white p-2 '><Link  to="/resume" >Resume</Link></li>
            </ul>
        </nav>
    )
}
